

.fsco {
    width: 100%;
    justify-content: space-evenly;
    align-items: center;
    display: flex;
}



/* code from head secetion css file  */

#menu {
    background: var(--accent);
    height: 77px;
    color: var(--text);
    display: flex;
    justify-content: space-around;
    align-items: center;
}
#navli {
    display: flex;
    justify-content: space-around;
    width: 40%;
    align-items: center;
}
#navli li {
    list-style: none;
}
.logo a, #navli li a {
    text-decoration: none;
    color: #FFF;
    font-weight: bold;
    font-size: 20px;
}
#drop {
    list-style: none;
    display: none;
}
.bi-list {
    scale: 150%;
}
.subdrop {
    position: absolute;
    height: 15%;
    border-radius: 8px;
    transform: translateY(50%);
    transform: translateX(-40%);
    width: 30%;
    background-color: rgb(0, 0, 0);
    display: none;
}
.subdrop li {
    list-style: none;
    height: 30px;
}
.subdrop li a {
    text-decoration: none;
    color: #FFF;
    font-weight: 800px;
}
@media (max-width: 780px) {
    #navli li {
        font-size: 50%;
    }
}
@media (max-width: 740px) {
    #navli {
        width: 60%;
    }
}
@media (max-width: 520px) {
    #navli {
        display: none;
    }
    #drop {
        display: block;
    }
    .licon:hover .subdrop {
        display: block;
     }
}