@font-face {
    font-family: tech;
    src: url(../font/tech.otf);
}


.angry-grid {
   display: grid; 
   grid-template-rows: 1fr 1fr 1fr 1fr;
   grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
   gap: 0px;
   height: 90vh;
}
  
#myimg {
    background-color: var(--accent);
    clip-path: circle(33.6% at 50% 50%);    
    overflow: hidden;
    grid-row-start: 1;
    grid-column-start: 5;
    grid-row-end: 5;
    grid-column-end: 9;
    display: flex;
    align-items: center;
    justify-content: space-around;  
}
#myimg img {
    width: 100%;
    scale: 60%;
}
#imtxt {
    background-color: var(--primary);
    margin-top: 20px;
    border-radius: 20px;
    height: fit-content;
    grid-row-start: 1;
   grid-column-start: 1;
   grid-row-end: 2;
   grid-column-end: 5;
   justify-content: center;
   align-items: center;
   display: flex;
}
#imtxt h1 {
    font-size: 60px;
    font-family: tech, Georgia, 'Times New Roman', Times, serif;
    color: var(--text);
}
#myname {
   grid-row-start: 2;
   grid-column-start: 1;
   grid-row-end: 5;
   grid-column-end: 5;
   justify-content: center;
   align-items: center;
   display: flex;
   flex-direction: column;
}
#myname h2 {
    font-size: 80px;
    color: var(--text);
    font-family: tech, Georgia, 'Times New Roman', Times, serif;
}
#myname h1 {
    font-size: 100px;
    color: var(--text);
    font-family: tech, Georgia, 'Times New Roman', Times, serif;
}
.sco {
    display: flex;
    transform: translateY(-80%);
    justify-content: space-around;
    align-items: center;
}
@media (max-width: 710px){
    .angry-grid {
        display: grid; 
     
        grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr;
        grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
        
        gap: 0px;
        height: 90vh;
        
    }     
     #imtxt {
        grid-row: 1 / 2;
        grid-column: 1 / 7;
    }
    #myname {
        grid-row: 5 / 7;
        grid-column: 1 / 7;
    }
    #myimg {
        scale: 160%;
        grid-row: 2 / 5;
        grid-column: 3 / 5;
    }
}
@media (max-width: 500px){
    #myimg {
        transform: translateY(30%);
        height: 40%;
        scale: 300%;
    }
    #myname h2 {
        font-size: 40px;
    }
    #myname h1 {
        font-size: 70px;
    }
    #imtxt h1 {
        font-size: 30px;
    }
}