* {
  text-align: center;
  margin: 0;
  padding: 0;
}

:root {
  --text: #010402;
  --background: #ecf9ef;
  --primary: #236c35;
  --secondary: #c9eed2;
  --accent: #39b157;
    
}

body {
    background-color: var(--background);
}
.err {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 20%;
  height: 90vh;
}

.gi {
  width: 100%;
}
.gi img {
  width: 40%;
}