

.test {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    text-align: center;
    padding: 20px;
}

.tesmo {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    box-shadow: 0px 2px 19px 9px rgba(0,0,0,0.5);
    width: 250px;
    padding: 30px;
    border-radius: 13px;
    height: 350px;
    background-color: var(--accent);
}

.tescad {
    display: flex;
    align-items: center;
    justify-content: space-around;
    gap: 30px;
    flex-wrap: wrap;
    padding: 20px;
}

.timg {
    -webkit-clip-path: circle(50% at 50% 50%);
    clip-path: circle(50% at 50% 50%);    
    width: 40%;
    overflow: hidden;
    height: 100px;
}
.timg img {
    width: 100%;
}
.tsname h1 {
    font-size: 25px;
}
.tsbtn {
    width: 200px;
    height: 50px;
}
.tsbtn a {
    width: 100%;
}
#tstbtn {
    width: 40%;
    height: 60%;
    border-radius: 5px;
    border: 1px solid black;
    background-color: rgba(255, 255, 255, 0);
    transition: 1s;
}
#tstbtn:hover {
    scale: 150%;
    box-shadow: 0px 2px 11px 9px rgba(0,0,0,0.5);
}