.port {
    padding: 40px;
    display: flex;
    gap: 40px;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
}

.portcad {
    width: 300px;
    height: 400px;
    background-color: var(--accent);
    border-radius: 12px;
    box-shadow: 0px 2px 11px 9px rgba(0, 0, 0, 0.5);
}

.portimg {
    width: 100%;
}

#potimg {
    width: 100%;
    border-radius: 12px;
}

#potbtn {
    position: absolute;
    transform: translateX(-50%);
    background-color: rgba(255, 235, 205, 0);
    border: black solid 1px;
    margin: auto;
    width: 80px;
    height: 40px;
    border-radius: 9px;
}

#potbtn:hover {
    box-shadow: 0px 2px 11px 9px rgba(0, 0, 0, 0.5);
    scale: 120%;
    border: 2px solid var(--accent);
}