

.servcd {
    display: flex;
    padding: 50px;
    flex-wrap: wrap;
    justify-content: space-around;
    align-content: center;
}
.servcad {
    width: 350px;
    border-radius: 10px;
    box-shadow: 0px 2px 17px 9px rgba(0,0,0,0.5);
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 300px;
    background-color: var(--secondary);
}